	module.exports = {"data":{"friendsJson":{"name":"Raggedy Anne","description":"<p>JoJo introduced me to her and her calf Baby Black Tip in 2012. The left side of her fluke is ragged along the edge. Her calf was fairly large, maybe two years old. His dorsal is black on the very end. BB Tip is somewhat interested in me and on occasion will swim up to me. Raggedy Anne keeps her distance unless Bo and Lemon Lips are around.  I have been able to observe BB Tip nursing. He is also learning to crater fish.</p>","featuredImage":{"src":{"publicURL":"/static/friend-pic-raggedy-de8e7241fc60ee8f4bdd9b82f8e28a30.jpg"},"caption":null},"images":[{"src":{"publicURL":"/static/ra2-c777aab5fe50030fcb1842fb747738f7.jpg"},"caption":"Raggedy Anne in estrus."},{"src":{"publicURL":"/static/ra3-6ecb8e9066bdb8200a7caf973cfae2bd.jpg"},"caption":"Raggedy Anne and Bo Pregnant 6/15."}],"videos":[{"src":"//player.vimeo.com/video/139251411","source":"vimeo"},{"src":"//www.youtube.com/embed/wGateJRZTjI?rel=0&fs=1&showinfo=1","source":"youtube"}]}},"pathContext":{}}

